<template>
    <ul>
        <li v-for="character in characters">
            <p>
                <strong>{{ character.name }}</strong>
                actor: <button class="search__innerLink"
                                @click="$emit('searchEvent', {apipath: apipathPerson, id: character.actorId})">
                                {{ character.actorName }}
                        </button>
                in movie <a :href="'/' + apipathMovie + character.movieId">{{ character.movieTitle }}</a>
            </p>
        </li>
    </ul>
</template>

<script>
    export default{
        props: ["characters", "apipathPerson", "apipathMovie"]
    }
</script>



