<template>
<section class="search">
  <div>
    <input type="text" id="query" v-model="query"/>
    <button type="button" @click="search('global_search?f=', query)">Find</button>
  </div>
  <hr/>
  <div class="result" v-if="result && apiPaths">
    <spoiler v-if="result.movies && result.movies.content.length"
                  :title="result.movies.title"
                  :count="result.movies.content.length">
      <movie-list :movies="result.movies.content" :apipath="apiPaths.movies"></movie-list>
    </spoiler>

    <spoiler v-if="result.characters && result.characters.content.length"
                  :title="result.characters.title"
                  :count="result.characters.content.length">
      <characters-list :characters="result.characters.content"
                       :apipath-movie="apiPaths.movies"
                       :apipath-person="apiPaths.persons"
                       @searchEvent="onSearch">
      </characters-list>
    </spoiler>

    <spoiler v-if="result.persons && result.persons.content.length"
                  :title="result.persons.title"
                  :count="result.persons.content.length">
      <data-list :data="result.persons.content"
                 value-name="name"
                 :apipath="apiPaths.persons"
                 @searchEvent="onSearch">
      </data-list>
    </spoiler>

    <spoiler v-if="result.productionCompanies && result.productionCompanies.content.length"
                  :title="result.productionCompanies.title"
                  :count="result.productionCompanies.content.length">
      <data-list :data="result.productionCompanies.content"
                 value-name="name"
                 :apipath="apiPaths.persons"
                 @searchEvent="onSearch">
      </data-list>
    </spoiler>

    <spoiler v-if="result.keywords && result.keywords.content.length"
                  :title="result.keywords.title"
                  :count="result.keywords.content.length">
      <cloud-list :data="result.keywords.content"
                  value-name="value"
                  :apipath="apiPaths.persons"
                  @searchEvent="onSearch">
      </cloud-list>
    </spoiler>

    <spoiler v-if="result.genries && result.genries.content.length"
                  :title="result.genries.title"
                  :count="result.genries.content.length">
      <cloud-list :data="result.genries.content"
                  value-name="value"
                  :apipath="apiPaths.persons"
                  @searchEvent="onSearch">
      </cloud-list>
    </spoiler>
</div>
</section>
</template>

<script>

// import DATA from "./data.js"
import Spoiler from "./components/Spoiler.vue";
import MovieList from "./components/MovieList.vue";
import CharacterList from "./components/CharacterList.vue"
import DataList from "./components/DataList.vue";
import CloudList from "./components/CloudList.vue"

// import Tabs from "./components/Tabs.vue"

export default {
  data () {
    return {
      apiName: "/movie_api",
      query: "",
      result: null,
      apiPaths: {
        movies: "movies/",
        persons: "movies_by_person/",
        genries: "movies_by_genry/",
        keywords: "movies_by_keyword/",
        productionCompanies: "movies_by_production_company/"
      }
    }
  },
  components:{
    "spoiler": Spoiler,
    "movie-list": MovieList,
    "characters-list": CharacterList,
    "data-list": DataList,
    "cloud-list": CloudList
  },
  methods:{
    search(path, param){
      console.log("fetch url: " + "/movie_api/" + path + param);
      fetch("/movie_api/" + path + param, {credentials: "include", method: "get"})
        .then(response => response.json())
        .then(data => {
          this.result = data;
        });
      // this.result = DATA;
      console.log(this.result);
    },
    onSearch(param){
        console.log(this.apiName + param.apipath +  + param.id);
        this.search(param.apipath, param.id);
    },
    url(group, id){
      return "/movie_api/" + group + "/" + id;
    }
    // ,
    // created(){
    //   console.log("init query: " + this.query);
    // }
  }
}
</script>

<style>

</style>



