<template>
    <div>
        <button v-for="elem in data"
                class="search__innerLink"
                @click="$emit('searchEvent', {apipath: apipath, id: elem.id})">
                {{ elem[valueName] }}
        </button>
    </div>
</template>

<script>
    export default{
        props: ["data", "valueName", "apipath"]
    }
</script>


