import Vue from 'vue'
import App from './App.vue'

var query_param = document.getElementById("app").getAttribute("data-query");
console.log(query_param);
new Vue({
  el: '#app',
  data(){
    return {
      query: query_param
    }
  },
  render: h => h(App)
})



