<template>
    <ul>
        <li v-for="elem in data">
            <button class="search__innerLink"
                    @click="$emit('searchEvent', {apipath: apipath, id: elem.id})">
                    {{ elem[valueName] }}
            </button>
        </li>
    </ul>
</template>

<script>
    export default{
        props: ["data", "apipath", "valueName"]
    }
</script>


