<template>
    <ul>
        <li v-for="movie in movies">
            <h3>{{ movie.title }}</h3>
            <p>{{ movie.tagline }}</p>
            <a :href="'/' + apipath + movie.id">More...</a>
        </li>
    </ul>
</template>

<script>
    export default {
        props: ["movies", "apipath"]
    }
</script>



