<template>
    <div>  
        <a :href="'#' + title" class="search__header" @click="show = !show">
            <h2>{{ title }}</h2>
            <span>results: {{ count }}</span>
        </a>
        <transition name="slide">
            <div class="slide" v-if="show">
                <slot/>
                <button @click="show = false">^ close ^</button>
            </div>
        </transition>
        <hr/>
    </div>
</template>

<script>
    export default {
        props: ["title", "count"],
        data() {
            return {
                show: false
            }
        }
    }
</script>

<style scoped>
.slide{
    transition: all 1.8s ease-in-out;
    box-sizing: border-box;
    margin: 0;
    overflow: hidden;
    max-height: 5000px;
}

.slide>ul{
    margin: 0;
}

.slide-enter, .slide-leave-to{
    max-height: 0;
}
</style>



